
#slider{
    position: relative;
    width: 800px;
}
.img {
    width: 100%;
    transition: width 500ms;
}
.button {
    position: absolute;
    top: 50%;
    font-size: 30px;
    line-height: 30px;
    font-weight: 900px;
    border: 2px solid black;
    border-radius: 50%;
    background-color: white;
    width: 30px;
    height: 30px;
    cursor: pointer;
    text-align: center;
}
.button:hover{
    background-color: beige;
}